// базовые подключения
@import "vars";
@import "mixins";
@import "fonts";
@import "settings";

// подключения компонентов страницы
@import "./components/header";
@import "./components/footer";
@import "./components/portfolio";
@import "./components/portfoliopage";
@import "./components/services";
@import "./components/about";
@import "./components/corp";


// подключения компонентов элементов на страницах
@import "./components/nav";
@import "./components/pagination";


.category__title {
    padding: 10px 0;
    margin-bottom: 40px;

    h1 {
        font-size: 32px;
        line-height: 48px;
        font-weight: 600;
        color: var(--color-black);
        margin-bottom: 20px;
        margin-top: 0;
    }

    p {
        color: var(--color-sub-text);
        font-size: 16px;
        line-height: 24px;
        font-weight: 600;
        margin: 0;
        max-width: 420px;
    }
}

.breadcrumbs {
    background-color: #F7F8F9;
    padding: 40px 0;
    margin-bottom: 50px;

    @include before('lg') {
        padding: 25px 0;
        margin-bottom: 30px;
    }

    @include before('sm') {
        padding: 15px 0;
        margin-bottom: 25px;
    }

    &__list {
        @include flex-v-center;
    }

    &__item {
        position: relative;
        text-transform: lowercase;
        color: var(--color-sub-text);
        font-size: 14px;
        line-height: 22px;

        &:not(:last-child) {
            padding-right: 20px;
            margin-right: 20px;

            &::after {
                content: '';
                height: 100%;
                width: 1px;
                border-radius: 50%;
                background: var(--color-green);
                opacity: 0.6;
                position: absolute;
                right: 0;
            }
        }
    }

    &__link {
        color: var(--color-sub-text);

        &:hover,
        &:focus {
            color: var(--color-green);
        }
    }
}

svg {
    width: 26px;
    height: 26px;
}