.header {

  padding: 25px 0;

  @include before('sm') {
    padding: 15px 0;
  }

  .container {
    @include flex-v-center;
  }

  &__logo {
    width: 50px;
    height: 50px;
    background-color: var(--color-green);
    display: block;
    cursor: pointer;
    border-radius: 4px;
  }

  .nav {
    margin-left: auto;

    @include before('sm') {
      display: none;
    }

  }

  &__button {
    @include flex-v-center;
    display: none;

    @include before('sm') {
      margin-left: auto;
      display: flex;
    }

    &-item {
      &:not(:last-child) {
        margin-right: 10px;
      }
    }

    svg {
      width: 23px;
      height: 23px;
      display: block;
      cursor: pointer;
    }
  }
}