.footer {
    background-color: #1F2A2E;
    padding: 25px 0;
    color: #F2F2F2;
    margin-top: auto;

    svg {
        fill: #F2F2F2;

        &:hover,
        &:focus {
            fill: --color-green;
        }
    }

    .container {
        @include flex-v-center;
    }

    &__social {
        margin-left: auto;
        @include flex-v-center;

        &-item {
            &:not(:last-child) {
                margin-right: 15px;
            }
        }

        &-link {

            svg {
                width: 20px;
                height: 20px;
                fill: #F2F2F2;

                &:hover,
                &:focus {
                    fill: var(--color-green);
                }
            }
        }
    }
}