.pagination {
    padding: 30px 0;
    margin: 20px 0 70px;

    &__list {
        @include flex-v-center;
    }

    &__item {

        &:not(:first-child),
        &:not(:last-child) {
            margin-right: 10px;
        }

        &:first-child {
            margin-right: auto;
        }

        &:last-child {
            margin-left: auto;
            margin-right: 0;
        }

        &:nth-last-child(-n+2) {
            margin-right: 0;
        }

        &:first-child,
        &:last-child {
            .pagination__link {
                padding: 5px;
            }
        }
    }

    &__link {
        font-size: 16px;
        line-height: 22px;
        font-weight: 600;
        color: var(--color-black);
        padding: 8px 14px;
        background-color: unset;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;

        &-active,
        &:hover,
        &:focus {
            background-color: #E9E9E9;
        }
    }

    &__work {
        background-color: #F7F8F9;
        padding: 37px 0;

        @include before('lg') {
            padding: 25px 0;
        }

        &-list {
            @include flex-all-sb;
        }

        &-link {
            font-size: 16px;
            line-height: 22px;
            font-weight: 600;
            color: var(--color-black);
            @include flex-v-center;
        }

        &-item {
            &:first-child {
                svg {
                    margin-right: 5px;
                    margin-left: -7px;
                }
            }

            &:last-child {
                svg {
                    margin-left: 5px;
                    margin-right: -7px;
                }
            }
        }
    }

}