.portfolio {
    &__tabs {
        margin-bottom: 40px;
        position: relative;

        &-list {
            @include flex-v-center;
        }

        &-item {
            font-size: 18px;
            font-weight: 600;
            line-height: 22px;
            text-decoration: none;
            position: relative;
            text-transform: lowercase;

            &:not(:last-child) {
                margin-right: 30px;

                @include before('sm') {
                    margin-right: 20px;
                }
            }

            &::after {
                content: '';
                position: absolute;
                bottom: -4px;
                width: 100%;
                left: 0;
                height: 2px;
                border-radius: 10px;
            }

            &:hover,
            &:focus {
                &::after {
                    background-color: var(--color-green);
                }
            }
        }

        &-link {
            color: var(--color-black);
        }
    }

    &__grid {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        grid-column-gap: 20px;
        // grid-auto-flow: dense;

        @include before('lg') {
            grid-template-columns: 1fr 1fr;
        }

        @include before('sm') {
            display: flex;
            flex-wrap: wrap;
            flex-direction: column;
        }
    }

    &__item {

        margin-bottom: 30px;

        /*&:nth-child(2n) {

            .portfolio__item-img {
                min-height: 400px;
                max-height: 400px;
            }
        }*/

        &-block {
            height: 100%;
            display: flex;
            flex-direction: column;
            text-align: center;
        }

        &-img {
            box-shadow: 0px 0px 6px rgba(51, 51, 51, 0.1);
            min-height: 250px;
            max-height: 250px;
            height: 100%;
            transition: 1s;

            img {
                height: 100%;
                object-fit: cover;
                width: 100%;
            }
        }

        &-title {
            font-size: 20px;
            line-height: 22px;
            font-weight: 700;
            color: var(--color-black);
            margin-top: 15px;
        }

        &-type {
            font-size: 14px;
            line-height: 21px;
            color: var(--color-sub-text);
            font-weight: 600;
            text-transform: lowercase;
            margin-top: 5px;
        }

        &:hover,
        &:focus {

            .portfolio__item-title {
                color: var(--color-green)
            }
        }
    }
}