 .corp {
     &__page {
         padding-bottom: 100px;
     }

     &__title {
         margin-bottom: 50px;
     }

     .cooperation {
         display: grid;
         grid-template-columns: 1fr 1fr;
         grid-gap: 30px;

         @include before('sm') {
             display: flex;
             grid-gap: 0;
             flex-direction: column;
         }

         &__input {
             background: #FFFFFF;
             border: 1px solid #E1E1E1;
             border-radius: 4px;
             padding: 15px;
             width: 100%;
         }

         &__field {
             width: 100%;

             @include before('sm') {
                 &:not(:last-child) {
                     margin-bottom: 20px;
                 }
             }

             &-textarea {
                 grid-column-start: 1;
                 grid-column-end: 3;
             }
         }

         &__textarea {
             min-height: 210px;
             max-height: 210px;
             height: 100%;
             width: 100%;
             min-width: 100%;
             max-width: 100%;
             border-radius: 4px;
             background: #FFFFFF;
             border: 1px solid #E1E1E1;
             padding: 15px;
         }

         &__button {
             border-radius: 4px;
             padding: 15px 25px;
             color: #fff;
             font-size: 16px;
             line-height: 24px;
             border: none;
             background-color: var(--color-green);
             cursor: pointer;
         }

         &__licenze {
             margin-top: 10px;
             margin-bottom: 0;
             font-size: 14px;
             line-height: 20px;
             color: var(--black);
         }
     }
 }