.nav {
    &__list {
        @include flex-v-center;
    }

    &__item {
        &:not(:last-child) {
            margin-right: 30px;
        }
    }

    &__link {
        font-weight: 600;
        color: var(--color-black);
        text-decoration: none;
        position: relative;
        transition: all 0.2s;

        &::after {
            content: '';
            height: 2px;
            width: 100%;
            position: absolute;
            bottom: -4px;
            border-radius: 50px;
            background-color: transparent;
            left: 0;
        }

        &:hover,
        &:focus,
        &-active {
            transition: all 0.2s;

            &::after {
                background-color: var(--color-green);
            }
        }
    }
}