.work__page {
    &-item {
        display: flex;
        flex-direction: column;
        padding-bottom: 100px;

        @include after('lg') {
            flex-direction: row;
        }

        .work__info {
            width: 100%;

            @include after('lg') {
                max-width: 380px;
                margin-right: 20px;
            }

            &-block {
                @include after('lg') {
                    position: sticky;
                    top: 20px;
                }
            }

            h1 {
                font-size: 32px;
                line-height: 48px;
                font-weight: 700;
                margin-top: 0;
                margin-bottom: 5px;

                @include before('sm') {
                    font-size: 28px;
                    line-height: 40px;
                    margin-bottom: 0;
                }
            }

            &-type {
                font-size: 16px;
                line-height: 24px;
                font-weight: 400;
                color: var(--color-sub-text);
                text-transform: lowercase;
            }

            &-text {
                margin-top: 30px;

                @include before('sm') {
                    margin-top: 20px;
                }

                p {
                    font-size: 16px;
                    line-height: 24px;
                    margin-bottom: 0;
                    margin-top: 0;

                    &:not(:last-child) {
                        margin-bottom: 10px;
                    }

                    @include before('sm') {
                        font-size: 14px;
                    }
                }
            }
        }

        .work__data {
            margin-top: 40px;

            @include before('sm') {
                margin-top: 30px;
            }

            &-item {
                margin-bottom: 25px;
                font-size: 18px;
                line-height: 22px;
                font-weight: 600;
                display: flex;
                flex-direction: column;

                span {
                    color: var(--color-sub-text);
                    font-size: 16px;
                    line-height: 22px;
                    font-weight: 400;
                    margin-bottom: 5px;
                }

                &:not(:last-child) {
                    margin-bottom: 25px;
                }
            }

            &-customer {
                // text-decoration: underline;
                // text-decoration-style: dotted;
                color: var(--color-black);
                width: max-content;
            }

            &-address {
                color: var(--color-green);
                width: max-content;
            }
        }

        .work__tool {
            display: flex;
            flex-direction: column;
            position: relative;
            padding-top: 25px;

            @include before('lg') {
                flex-direction: row;
                justify-content: space-between;
                margin-bottom: 30px;
                max-width: 345px;
            }

            &::before {
                content: '';
                height: 1px;
                width: 180px;
                display: block;
                border-radius: 10px;
                background-color: rgba(136, 136, 136, 0.2);
                position: absolute;
                top: 0;
                left: 0;

                @include before('lg') {
                    width: 345px;
                }

                @include before('sm') {
                    width: 100%;
                }
            }

            &-date {
                font-size: 14px;
                line-height: 22px;
                @include flex-v-center;
                color: var(--color-black);
                width: max-content;

                &:not(:last-child) {
                    margin-bottom: 15px;

                    @include before('lg') {
                        margin-bottom: 0;
                    }
                }

                svg {
                    width: 24px;
                    height: 24px;
                    margin-right: 10px;
                    fill: var(--color-black);
                }

                &:hover,
                &:focus {
                    color: var(--color-green);

                    svg {
                        fill: var(--color-green);
                    }
                }
            }
        }

        .work__screenshots {

            @include before('lg') {
                width: 100vw;
                overflow-x: scroll;
                scroll-snap-type: x proximity;
                overscroll-behavior-x: contain;
                display: flex;
                flex-wrap: nowrap;
                scroll-padding: 20px;
                justify-content: flex-start;
                scrollbar-width: none;
                margin-left: -15px;
                padding-left: 15px;
            }

            &-item {
                box-shadow: 0px 0px 6px rgba(51, 51, 51, 0.2);
                max-height: 450px;
                height: 100%;
                width: 100%;
                border-radius: 4px;

                &:not(:last-child) {
                    margin-bottom: 30px;

                    @include before('lg') {
                        margin-bottom: 0;
                    }
                }

                @include before('lg') {
                    min-height: 130px;
                    min-width: 225px;
                    margin-bottom: 0;
                    margin-right: 20px;
                }
            }

            &-img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                border-radius: 4px;
            }
        }
    }
}